.clearfix2 {
  *zoom: 1;
  clear: both;
}
.clearfix2:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
  zoom: 1;
}
.clearfix::before,
.clearfix::after {
  content: "";
  display: table;
}
header {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: 100%;
  background-color: #1f74f1;
}
header .header-fil {
  display: flex;
  width: 1200px;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
}
header .header-fil .logo {
  display: flex;
  flex: 0 0 136px;
  width: 136px;
  height: 40px;
  background: url("../images/logo.png") no-repeat;
}
header .header-fil .logo img {
  vertical-align: middle;
}
header .header-fil .header-icon {
  font-size: 18px;
  display: flex;
  flex: 0 0 75%;
  flex-wrap: nowrap;
  margin-top: 9px;
  margin-left: 5%;
}
header .header-fil .header-icon li {
  height: 34px;
  text-align: center;
  cursor: pointer;
}
header .header-fil .header-icon li a {
  color: white;
  padding: 6px 16px;
}
header .header-fil .header-icon li.current a {
  border-radius: 2.5rem;
  text-align: center;
  align-items: center;
  background-color: #155ec7;
}
header .header-fil .header-icon li:hover a {
  border-radius: 2.5rem;
  text-align: center;
  align-items: center;
  background-color: #155ec7;
}
header .header-fil .user-icon {
  display: flex;
  justify-content: flex-end;
  position: relative;
  font-size: 14px;
  flex: 0 0 80px;
}
header .header-fil .user-icon:hover .icon-show ul {
  display: block;
}
header .header-fil .user-icon .me {
  color: white;
}
header .header-fil .user-icon .me span:before {
  content: '';
  display: inline-block;
  background: url(../images/btn_02.png) no-repeat scroll 0px 2px;
  width: 15px;
  height: 15px;
  margin-right: 7px;
}
header .header-fil .user-icon .me span:after {
  content: '';
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 6px solid white;
  display: inline-block;
  margin-left: 7px;
}
header .header-fil .user-icon .icon-show {
  padding-top: 20px;
}
header .header-fil .user-icon .icon-show ul {
  width: 89px;
  background-color: white;
  position: absolute;
  left: 0;
  z-index: 100;
  box-shadow: 0 0 10px rgba(50, 52, 56, 0.4);
  display: none;
}
header .header-fil .user-icon .icon-show ul li {
  line-height: 35px;
}
header .header-fil .user-icon .icon-show ul li:hover {
  background-color: #f0f0f0;
}
header .header-fil .user-icon .icon-show ul li:nth-of-type(1) a:before {
  content: '';
  display: inline-block;
  width: 17px;
  height: 24px;
  background: url(../images/btn_02.png) no-repeat scroll -22px 3px;
  vertical-align: middle;
  margin-left: 12px;
  margin-right: 11px;
}
header .header-fil .user-icon .icon-show ul li:nth-of-type(2) a:before {
  content: '';
  display: inline-block;
  width: 17px;
  height: 24px;
  vertical-align: -11px;
  margin-left: 12px;
  margin-right: 11px;
  background: url(../images/btn_02.png) no-repeat scroll -48px 0;
}
header .header-fil .user-icon .icon-show ul li a {
  width: 100%;
  display: inline-block;
  color: #989898;
}
