.magic-radio {
  position: absolute;
  display: none;
}
.magic-radio[disabled] {
  cursor: not-allowed;
}
.magic-radio + label {
  position: relative;
  display: block;
  padding-left: 30px;
  cursor: pointer;
  vertical-align: middle;
}
.magic-radio + label:before {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 18px;
  height: 18px;
  content: '';
  border: 1px solid #c0c0c0;
}
.magic-radio + label:after {
  position: absolute;
  display: none;
  content: '';
}
.magic-radio[disabled] + label {
  cursor: not-allowed;
  color: #e5e5e5;
}
.magic-radio[disabled] + label:before {
  border-color: #e4e4e4;
}
.magic-radio:checked + label:before {
  animation-name: none;
}
.magic-radio:checked + label:after {
  display: block;
}
.magic-radio + label:before {
  border-radius: 50%;
}
.magic-radio + label:after {
  top: 6px;
  left: 6px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #3e97eb;
}
.magic-radio:checked + label:before {
  border: 1px solid #e5e5e5;
}
.magic-radio:checked[disabled] + label:before {
  border: 1px solid #e5e5e5;
}
.magic-radio:checked[disabled] + label:after {
  background: #c9e2f9;
}
